











import {Vue, Component} from 'vue-property-decorator'

const myPlugins = {
    install (Vue) {
        Vue.mixin({
            methods: {
                hello () {
                    this.$notify({
                        title: 'Plugins',
                        message: 'hi'
                    })
                }
            }
        })
    }
}

Vue.use(myPlugins)

@Component
export default class Examples extends Vue {
}
